import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'

import queryBlockChain from './views/queryChain/queryBlockChain.vue'

import queryLoanDetailInfo from './views/queryChain/queryLoanDetailInfo.vue'
import queryAssertDetail from './views/queryChain/queryAssertDetail.vue'
import queryPayPlanDetailInfo from './views/queryChain/queryPayPlanDetailInfo.vue'
import queryPayLogDetailsInfo from './views/queryChain/queryPayLogDetailsInfo.vue'
import querySpecialBusinessDetail from './views/queryChain/querySpecialBusinessDetail.vue'
import queryCreditDetail from './views/queryChain/queryCreditDetail.vue'
import queryCreditInfo from './views/queryChain/queryCreditInfo.vue'
import queryAcctFlowDetail from './views/queryChain/queryAcctFlowDetail.vue'

import queryAcctFlowInfo from './views/queryChain/queryAcctFlowInfo.vue'
import queryBfjFlowInfo from './views/queryChain/queryBfjFlowInfo.vue'
import queryAcctInfo from './views/queryChain/queryAcctInfo.vue'
import queryAcctDetail from './views/queryChain/queryAcctDetail.vue'
import queryBfjSumInfo from './views/queryChain/queryBfjSumInfo.vue'
import queryFeeInfo from './views/queryChain/queryFeeInfo.vue'
import queryRevTransInfo from './views/queryChain/queryRevTransInfo.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
//          { path: '/main', component: Main, name: '', hidden: true },
            { path: '/queryBlockChain', component: queryBlockChain, name: '' },          
        ]
    },
        {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/queryLoanDetailInfo', component: queryLoanDetailInfo, name: '' }, 
            { path: '/queryCreditInfo', component: queryCreditInfo, name: '' },
            {path:'/queryAssertDetail',component: queryAssertDetail, name:"" ,hidden:true},
            {path:'/queryPayPlanDetailInfo',component: queryPayPlanDetailInfo, name:"" ,hidden:true},
            {path:'/queryPayLogDetailsInfo',component: queryPayLogDetailsInfo, name:"" ,hidden:true},
            {path:'/querySpecialBusinessDetail',component: querySpecialBusinessDetail, name:"" ,hidden:true},
            {path:'/queryCreditDetail',component: queryCreditDetail, name:"" ,hidden:true},
            {path:'/queryAcctFlowDetail',component: queryAcctFlowDetail, name:"" ,hidden:true},
        ]
    },
            {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/queryAcctFlowInfo', component: queryAcctFlowInfo, name: '' }, 
            { path: '/queryBfjFlowInfo', component: queryBfjFlowInfo, name: '' },
            {path:'/queryAcctInfo',component: queryAcctInfo, name:"" },
            {path:'/queryAcctDetail',component: queryAcctDetail, name:"" ,hidden:true},
            {path:'/queryBfjSumInfo',component: queryBfjSumInfo, name:""},
            {path:'/queryFeeInfo',component: queryFeeInfo, name:""},
            {path:'/queryRevTransInfo',component: queryRevTransInfo, name:""},
        ]
    },
    
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }    
];

export default routes;


