<template>
<div>
		<div class="title">
			<el-button size="middle" @click="back()" class="but1" style="background-color: aliceblue"></el-button>
			<el-button size="middle" @click="payPlan()" class="but2" style="background-color: aliceblue;"></el-button>
			<el-button size="middle" @click="payLog()" class="but3" style="background-color: aliceblue"></el-button>
			<el-button size="middle" @click="specialBusiness()" class="but4" style="background-color: aliceblue"></el-button>
			<el-button size="middle" @click="acctFlow()" class="but5" style="background-color: aliceblue" v-loading="listLoading"></el-button>

		</div>
		<template v-if="loanDetail!=null && loanDetail!=''">
			<table class="tab1">
				<tr>
					<td class="title1"></td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{loanDetail.loanNumber}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.loanDate}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.loanEndDate}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.loanPurpose}}</td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{loanDetail.loanType}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.borrowerType}}</td>
				    <td></td><td class="loanDetailPart">{{loanDetail.rate}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.rateType}}</td>
				</tr>	
				<tr >
					<td></td><td class="loanDetailPart">{{loanDetail.productName}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.productNo}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.transactionDate}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.stopInterestFlag}}</td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{loanDetail.payMethod}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.repaymentPeriod}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.firstPayDate}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.maxDelayDays}}</td>
				</tr>	
				<tr>
					<td></td><td class="loanDetailPart">{{loanDetail.repaidPeriod}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.settleOnDate}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.transactionFlowNo}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.periods}}</td>				
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{loanDetail.customerNo}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.contractNo}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.guaranteeMethod}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.customerName}}</td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{loanDetail.accountState}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.loanAmount}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.normalBalance}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.delayBalance}}</td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{loanDetail.penaltyRate}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.penaltyAmount}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.debitInterestAmount}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.receiverBankNo}}</td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{loanDetail.receiverBankType}}</td>
					<td></td><td class="loanDetailPart">{{loanDetail.receiverName}}</td>
				</tr>
			</table>
		</template>
		<template v-if="customerDetail!=null && customerDetail!=''">
			<table class="tab1">
				<tr>
					<td class="title1"></td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{customerDetail.customerNo}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.name}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.sex}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.certificateType}}</td>
				</tr>
				<tr>
				   <td></td><td class="loanDetailPart">{{customerDetail.address}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.certificateNum}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.birthday}}</td>
				    <td></td><td class="loanDetailPart">{{customerDetail.phone}}</td>
				</tr>	
				<tr >
					<td></td><td class="loanDetailPart">{{customerDetail.district}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.city}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.province}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.linkMan}}</td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{customerDetail.linkManPhone}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.linkManRelation}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.companyName}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.companyType}}</td>
				</tr>	
				<tr >
					<td></td><td class="loanDetailPart">{{customerDetail.companyAddress}}</td>
					<td>-</td><td class="loanDetailPart">{{customerDetail.companyDistrict}}</td>
					<td>-</td><td class="loanDetailPart">{{customerDetail.companyCity}}</td>
					<td>-</td><td class="loanDetailPart">{{customerDetail.companyProvince}}</td>				
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{customerDetail.homeAddress}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.addressCode}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.communicationCode}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.companyCode}}</td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{customerDetail.maritaStatus}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.education}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.income}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.mobile}}</td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{customerDetail.job}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.jobLeval}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.jobTitle}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.updateFlag}}</td>
				</tr>
				<tr>
					<td></td><td class="loanDetailPart">{{customerDetail.receiverBankType}}</td>
					<td></td><td class="loanDetailPart">{{customerDetail.cardStartDate}}</td>
				    <td></td><td class="loanDetailPart">{{customerDetail.cardEndDate}}</td>				
				</tr>
			</table>
		</template>
	</div>	
</template>

<script>
    import util from '../../common/js/util'
	import {requestQueryAcctFlow} from '../../api/api';
	export default {
		data() {
			return {
					assertData:'',
					loanDetail:'',
					customerDetail:'',
					payPlanDetail:'',
					payLogListDetail:[],
					specialBusinessListDetail:[],
					acctFlows:[],
				    listLoading: false,
			}
		},
		methods: {
			//
			getParams:function(){
				if(this.$route.params.param != null){
					//
					this.assertData = this.$route.params.param;
					//
					this.loanDetail = this.$route.params.param.loanDetailInfo;
					//
					this.customerDetail = this.$route.params.param.customerInfo;
					//
					this.payPlanDetail = this.$route.params.param.payPlanInfo;
					//
					this.payLogListDetail = this.$route.params.param.payLogListInfo;
					//
					this.specialBusinessListDetail = this.$route.params.param.specialBusinessListInfo;
					
				    let loanDate = this.$route.params.loanData;
					if(loanDate != null && loanDate!==""){
						sessionStorage.setItem('loanDate', loanDate);
					}
					let loanNumber = this.$route.params.loanNumber;
					if(loanNumber!= null && loanNumber !== ""){
						sessionStorage.setItem('loanNumber', loanNumber);
					}
				}
			},
			back:function(){
				history.go(-1);
			},
			payPlan:function(){
				this.$router.push({
				   name: '',
				   params:{param:this.assertData},
			     });
			     
			},
			payLog:function(){
				this.$router.push({
				   name: '',
				   params:{param:this.assertData},
			     });
			},
			specialBusiness:function(){
				this.$router.push({
				   name: '',
				   params:{param:this.assertData},
			     });
				
			},
			acctFlow:function(){
				let loanNumber = '';
				if(this.loanDetail  != null){
					loanNumber = this.loanDetail .loanNumber
				}
				let para =  JSON.stringify({
					loanNumber: loanNumber,
				});
				this.listLoading = true;
				requestQueryAcctFlow(para).then((res) => {
					let data = res.data;
					if(res.returnCode="000000" && data!=null && data!="" && Array.isArray(JSON.parse(data))){
						let dataObj = JSON.parse(data);	
					    this.acctFlows = dataObj;
					    this.$router.push({
						   name: '',
						   params:{param:this.assertData,acctparam:this.acctFlows},
			     });
					}else if(res.returnCode="000000" && data!=null && data!="" && !Array.isArray(JSON.parse(data))){
					    	let dataObj = JSON.parse(data);
					    	this.acctFlows.push(dataObj);
					    	this.$router.push({
							   name: '',
							   params:{param:this.assertData,acctparam:this.acctFlows},
						     });
					}else if(res.returnCode!="000000"){
						alert(res.returnMsg);
					}
					this.listLoading = false;
				}).catch((error) => {
					 console.log(error)
					 this.listLoading = false;
				});				
			},

        },
	     mounted: function() {
	        if(sessionStorage.getItem("ass") != null && sessionStorage.getItem("ass")!="undefined" && sessionStorage.getItem("ass")!=undefined ){
	        	this.assertData = JSON.parse(sessionStorage.getItem("ass"));
	        	this.loanDetail = JSON.parse(sessionStorage.getItem("ass")).loanDetailInfo;
				//
				this.customerDetail = JSON.parse(sessionStorage.getItem("ass")).customerInfo;
				//
				this.payPlanDetail = JSON.parse(sessionStorage.getItem("ass")).payPlanInfo;
				//
				this.payLogListDetail = JSON.parse(sessionStorage.getItem("ass")).payLogListInfo;
				//
				this.specialBusinessListDetail = JSON.parse(sessionStorage.getItem("ass")).specialBusinessListInfo;
				sessionStorage.removeItem("ass");
	        }else{
	        	this.getParams();
	        }

		}
 }	
</script>

<style>
	.tab1{
		margin-top: 15px;
		/*background-color: gainsboro;*/
	}
	.title1{
		font-size: 18px;
		color: blue;
	}
   .loanDetailPart{
		padding-top: 5px;
    	padding-bottom: 5px;
    	color:blue;
    	text-align:left;
	}
	.but1{
		margin-right: 100px;
	}
	.but2{
		margin-right: 60px;
	}
	.but3{
		margin-right: 60px;
	}
	.but4{
		margin-right: 60px;
	}
</style>


